@import url(https://fonts.googleapis.com/css?family=Be+Vietnam|Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: rgb(243, 243, 243);
}
* {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
.card-header {
  background: #fff !important;
  border-bottom: solid 1px rgb(245, 245, 245);
}
.card {
  border: none;
  margin: 1% 2%;
  box-shadow: 1px 1px 10px rgb(230, 230, 230);
}
.title-hr {
  width: 80px;
  border-radius: 10px;
  background: rgb(255, 204, 109);
  margin: auto;
  height: 20px;
}
.normal-hr {
  width: 10%;
  margin: auto;
  margin-bottom: 1%;
  border-radius: 5px;
  background: rgb(255, 204, 109);
}
.title {
  color: #fff;
  text-shadow: 1px 1px 10px #000;
  text-transform: capitalize;
}
#videoBG {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -100;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-aspect-ratio: 16/9) {
  #videoBG {
    width: 100%;
    height: auto;
  }
}
@media (max-aspect-ratio: 16/9) {
  #videoBG {
    width: auto;
    height: 100%;
  }
}

.intro {
    height: 100vh;
    background-image: url(/static/media/mac.046c9635.jpg);
    background-size: cover;
    overflow: hidden;
    -webkit-filter: drop-shadow(0px 2px 0px rgba(244, 247, 249, 1));
            filter: drop-shadow(0px 2px 0px rgba(244, 247, 249, 1));
    cursor: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/9632/happy.png"), auto;
    font-family: "Be Vietnam", sans-serif;
  }
  .intro .title-text {
    background: #fff;
    height: 100vh;
    padding-top: 150px !important;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(251, 251, 251, 0.1)), color-stop(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5)));
    background: linear-gradient(rgba(251, 251, 251, 0.1), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
    color: rgb(245, 245, 245);
  }
  @media screen and (max-width: 768px) {
    .intro .title-text {
      font-size: 12px;
    }
    .intro h1 {
      font-size: 20px;
    }
  }
  .intro i {
    font-family: "Pacifico", cursive;
    font-weight: lighter;
  }
  .typewriter {
    color: #fff;
    font-family: monospace;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: 0.15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: 0.15em; /* Adjust as needed */
    -webkit-animation: typing 3.5s steps(10, end), blink-caret 0.5s step-end;
            animation: typing 3.5s steps(10, end), blink-caret 0.5s step-end;
  }
  
  /* The typing effect */
  @-webkit-keyframes typing {
    from {
      width: 50%;
    }
    to {
      width: 60%;
    }
  }
  @keyframes typing {
    from {
      width: 50%;
    }
    to {
      width: 60%;
    }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: orange;
    }
  }
  @keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: orange;
    }
  }
  
  .fa-arrow-circle-down {
    bottom: 0;
    color: red;
  }
  .rank-label-container {
    margin-top: -19px;
    text-align: center;
  }
  
  .rank-label {
    padding: 5px 10px 5px 10px;
    border-radius: 7px;
  }
  .about-info {
    padding: 4%;
  }
  
.lined .nav-link {
    border: none;
    color: #555;
    border-bottom: 1px solid transparent;
  }
  
  .lined .nav-link:hover {
    border: none;
    border-bottom: 1px solid transparent;
  }
  
  .lined .nav-link.active {
    background: none;
    color: orange;
    border-color:orange;
  }
